import React from 'react';
import Button from '../Button/Button.js'
import './Section2.scss';

const Section2 = ({title, imgSrc, imgName}) => {

    return(
        <section className="section-2">
            <div className='section-2__texts'>
                <h1 className='section-2__texts__title'>{title}</h1>
                <p className='section-2__texts__text'>
                    Na PortuGO, somos pioneiros da inovao em mobilidade urbana, 
                    dedicados a redefinir as vias do transporte moderno em Portugal. 
                    Nossa misso  oferecer uma alternativa de transporte acessvel 
                    e de alta qualidade que se adapte s necessidades dinmicas dos 
                    passageiros do sculo XXI.
                </p>
                <p className='section-2__texts__text'>
                    Com uma frota de veculos modernos e uma interface de aplicativo intuitiva, 
                    garantimos um servio eficiente e confortvel para cada viagem.
                </p>
                <Button styleType={'btn--style-1'} text={'Saiba mais'} icon={''} />
            </div>
            <img className='section-2__img' src={imgSrc} alt={imgName} />
        </section>
    )
}

export default Section2;