import React from 'react';
import './Section5.scss';
import PortugalMap from '../PortugalMap/PortugalMap';

const Section5 = ({ title }) => {
    return(
        <section className='section-5'>
            <h1 className='section-5__title'>{title}</h1>
            <div className='section-5__div'>
                <PortugalMap className='section-5__div__img' />
                <div className='section-5__div__texts'>
                    <p className='section-5__div__texts__text'>
                        Nossos servios cobrem as principais cidades em todas as regies de Portugal. 
                        Seja nos agitados centros urbanos ou nas cidades mais interioranas do pas, 
                        a PortuGO est aqui para lev-lo onde voc precisa, quando voc precisa.
                    </p>
                    <p className='section-5__div__texts__text'>
                        A nossa rede de motoristas est distribuda conforme  demonstra o mapa ao lado. 
                        Estamos numa constante expanso das nossas operaes, onde miramos em tornar o 
                        transporte urbano em Portugal barato aos clientes e justo aos motoristas. 
                    </p>
                </div>
            </div>
        </section>
    )
}

export default Section5;