import './Section3.scss';

const Section3 = ({ title }) => {
    return(
        <section className='section-3'>
            <h1 className='section-3__title'>{title}</h1>
            <div className="section-3__benefits">
                <div className="section-3__benefits__benefit">
                    <img className="section-3__benefits__benefit__img" src='images/ic-calendar-clock.svg' alt='cone de calendrio' />
                    <div className="section-3__benefits__benefit__texts">
                        <h2 className="section-3__benefits__benefit__texts__title">Decida sua jornada</h2>
                        <p className="section-3__benefits__benefit__texts__text">
                            s livre para decidir seu horrio de trabalho. Seja pela manh, tarde ou noite, 
                            deixamos que voc tenha autonomia completa para conduzir em nossa app a qualquer hora.
                        </p>
                    </div>
                </div>
                <div className="section-3__benefits__benefit">
                    <img className="section-3__benefits__benefit__img" src='images/ic-payments.svg' alt='cone de dinheiro' />
                    <div className="section-3__benefits__benefit__texts">
                        <h2 className="section-3__benefits__benefit__texts__title">Ganhe por viagem</h2>
                        <p className="section-3__benefits__benefit__texts__text">
                            Nossos motoristas so pagos a cada viagem realizada  e no no final de ms. 
                            Nossa app tambm possui um sistema de gorjeta, que permite passageiros concederem 
                            um bnus aos motoristas.
                        </p>
                    </div>
                </div>
                <div className="section-3__benefits__benefit">
                    <img className="section-3__benefits__benefit__img" src='images/ic_partner_exchange.svg'  alt='cone de colaborao' />
                    <div className="section-3__benefits__benefit__texts">
                        <h2 className="section-3__benefits__benefit__texts__title">Parceiros dos trabalhadores</h2>
                        <p className="section-3__benefits__benefit__texts__text">
                            Acreditamos numa troca justa entre nossas frotas e a nossa companhia. 
                            Somos reconhecidos como a melhor app de transporte urbano para motoristas em toda Portugal. 
                        </p>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Section3;